import "./details.css";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import { toast } from "react-toastify";

const ProductDetails = () => {
  let params = useParams();
  let navigate = useNavigate();
  let dispatch = useDispatch();
  let userProducts = useSelector((store) => store.ProductSection.products);
  let loggedUser = useSelector((store) => store.AuthSection).loggedUser;

  let item = userProducts.find((a1) => a1.id === params.id);

  let [img, setImg] = useState(item.img);

  let size = [
    "6",
    "6.5",
    "7",
    "7.5",
    "8",
    "8.5",
    "9",
    "9.5",
    "10",
    "11",
    "11.5",
    "12",
  ];

  function handleSize(evt) {
    item.size = evt.target.innerText;
    item.owner = loggedUser.email;
    console.log(item.size, item.owner, "login info", loggedUser);
    //   evt.target.classList.remove("border");
    // console.log(item);
  }
  return (
    <div>
      <div className="container my-sm-5 my-3">
        <div className="row ">
          <div className="col-12 col-md-7 p-0 mb-3">
            <div className="row m-0">
              <div className="col-12 col-sm-3 pe-0">
                <img
                  src={item.img}
                  onClick={() => {
                    setImg(item.img);
                  }}
                  alt="..."
                  className="col-4 col-sm-12 mb-2 pe-2"
                />
                <img
                  src={item.img2}
                  onClick={() => {
                    setImg(item.img2);
                  }}
                  alt="..."
                  className="col-4 col-sm-12 mb-2 pe-2"
                />
                <img
                  src={item.img3}
                  onClick={() => {
                    setImg(item.img3);
                  }}
                  alt="..."
                  className="col-4 col-sm-12 mb-2 pe-2"
                />
              </div>
              <div className="col-12 col-sm-9">
                <img src={img} alt={img + ".."} className="w-100" />
              </div>
            </div>
          </div>
          <div className="col-12 ms-auto col-md-5">
            <div>
              <h2>{item.title}</h2>
              <p>{item.dic}</p>
              <div className="d-flex flex-wrap text-muted fw-semibold">
                Old-Price:
                <span className="text-decoration-line-through me-3  ">
                  ${item.price}
                </span>
                <span className="mx-4 ms-sm-0 text-danger ">
                  New-Price:${item.price - item.price * 0.2}
                </span>
                <span className="text-success">Discount:20%</span>
              </div>
              <p style={{ color: "#aaa" }}>
                incl of taxes <br /> (also incl all duty charges)
              </p>
            </div>
            <h3>Select Size</h3>
            <div className="p-0">
              <div className="row w-100 gap-1 m-0">
                {size.map((sizeValue) => {
                  return (
                    <button
                      onClick={(evt) => {
                        handleSize(evt);
                      }}
                      key={sizeValue}
                      className=" focus-bgColor    col-3 border-secondary   rounded p-1 text-center fw-semibold"
                    >
                      UK {sizeValue}
                    </button>
                  );
                })}
              </div>
            </div>
            <div className="mt-5">
              <button
                className="btn btn-dark rounded-pill d-block w-100 my-2"
                onClick={(event) => {
                  if (item.size && item.owner) {
                    dispatch({ type: "ADD_TO_CART", payload: item });
                    navigate("/cart");
                  } else {
                    toast.warning("Select Size");
                  }
                }}
              >
                Add To Cart
              </button>
              <button
                onClick={(event) => {
                  console.log("online");
                }}
                className="btn btn-outline-dark border border-2 border-dark rounded-pill d-block w-100 my-2"
              >
                Online Payment
              </button>
            </div>
            <div className="mt-4">
              <h4>Product Details</h4>
              <p>
                {"The " +
                  item.title.toLowerCase() +
                  " is very comfortable and soft shoes easy to wear and good for walk."}
              </p>
            </div>
          </div>
        </div>
      </div>

      {/* {Array.from({ length: 4 }).map((_, idx) => (         ))} */}
    </div>
  );
};

export default ProductDetails;
