import banner from "../../Images/Banner.webp";

import Card from "../card/card";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";

export default function Home() {
  let branders = [
    {
      image:
        "https://shoe-paradies.vercel.app/_next/image?url=%2Fshoe.avif&w=1200&q=75",
      name: "adidas",
    },
    {
      image:
        "https://shoe-paradies.vercel.app/_next/image?url=%2Fshoe2.avif&w=1200&q=75",
      name: "nike",
    },
    {
      image:
        "https://shoe-paradies.vercel.app/_next/image?url=%2Fshoe3.avif&w=1200&q=75",
      name: "bata",
    },
    {
      image:
        "https://shoe-paradies.vercel.app/_next/image?url=%2Fshoe.avif&w=1200&q=75",
      name: "service",
    },
  ];
  let userProducts = useSelector((store) => store.ProductSection.products);

  return (
    <>
      {/* banner large image */}
      <div className="container-fluid p-0">
        <img src={banner} alt="banner" className="w-100" />
      </div>
      {/* brands  */}
      <div className="container-fluid mt-4 d-flex gap-3">
        {branders.map((item) => {
          return (
            <Link
              to={"/brands/" + item.name}
              className="w-25 brand-container"
              key={item.name}
            >
              <img src={item.image} alt="brand" width="100%" />
              <p className="text-dark brand-name fw-bold text-capitalize">
                {item.name}
              </p>
            </Link>
          );
        })}
      </div>
      {/* heading for multiple */}
      <h1 className="my-4 d-flex  heading px-3 px-md-4 px-lg-5">
        <span className="h3 me-auto me-sm-0">--Products</span>
        <span className="h2 d-sm-flex d-none me-auto text-light px-3 px-md-4 px-lg-5">
          Check our new product
        </span>
        <Link to="/brands/all" className="h6 view-all text-secondary">
          View All
        </Link>
      </h1>

      {/* cards  */}
      <div className="row w-100 m-0 px-3 px-md-4 px-lg-5">
        {userProducts
          ? userProducts.map((product) => {
              return <Card key={product.id} item={product} />;
            })
          : null}
      </div>
      {/* cards closed */}
    </>
  );
}
