import { Link } from "react-router-dom";

export default function Card({ item }) {
  return (
    <div className="col-12 col-sm-6 col-md-4 col-xl-3 mb-4">
      <Link to={"/details/" + item.id} className="card text-dark border-0">
        <img src={item.img} className="card-img-top" alt="Sneakers" />
        <div className="card-body">
          <h5 className="card-title ">{item.title}</h5>
          <p className="card-text">{item.dic}</p>
          <div className="d-flex ">
            <span className="text-decoration-line-through me-3 fw-semibold text-muted">
              {item.price}$
            </span>
            <span className="me-auto text-danger fw-semibold">
              {item.price - item.price * 0.2}$
            </span>
            <span className="text-success fw-semibold">20%</span>
          </div>
        </div>
      </Link>
    </div>
  );
}
