import "./app.css";

import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

import { Route, Routes } from "react-router-dom";
import Navbar from "./components/navbar/navbar";
import Landing from "./components/landing/Landing";
import Login from "./components/login/login";
import Admin from "./components/admin/admin";
import Home from "./components/home/home";
import Brands from "./components/brands/brand";
import Footer from "./components/footer/footer";
import Signup from "./components/sign-up/signup";
import ProductDetails from "./components/product-details/details";
import Checkout from "./components/check-out/check-out";

import { useDispatch, useSelector } from "react-redux";
import Pending from "./components/pending-orders/pending";
// import Table from "./components/table/table";
// import Carousel from "./components/carousel/carousel";

import { gql, useQuery, useLazyQuery } from "@apollo/client";

export default function App() {
  let loggedUser = useSelector((store) => store.AuthSection.loggedUser);
  let dispatch = useDispatch();

  let LS_token = localStorage.getItem("userId");

  const User_Token = gql`
    query ($token: String!) {
      checkToken(token: $token) {
        _id
        email
        password
        first
        last
      }
    }
  `;
  const { data, loading, error } = useQuery(User_Token, {
    variables: { token: LS_token },
    onCompleted: (data) => {
      if (data?.checkToken) {
        dispatch({
          type: "USER_LOGIN_HOGYA",
          payload: data.checkToken,
        });
      }
    },
  });

  if (error) {
    console.log(error);
  }
  if (data) {
    console.log(data, "Autherization by token .....");
  }

  return (
    <>
      <Navbar />
      <div style={{ minHeight: "60vh" }}>
        <Routes>
          <Route path={"/"} element={<Landing />} />
          <Route path="/login" element={<Login />} />
          <Route path="/signup" element={<Signup />} />
          <Route path="/admin" element={<Admin />} />
          {loggedUser ? (
            <>
              <Route path="/home" element={<Home />} />
              <Route path="/brands/:brand" element={<Brands />} />
              <Route path="/details/:id" element={<ProductDetails />} />
              <Route path="/cart" element={<Checkout />} />
              <Route path="/pending" element={<Pending />} />
            </>
          ) : null}
        </Routes>
      </div>
      <Footer />
      <ToastContainer />
    </>
  );
}
