import { useSelector } from "react-redux";
import Table from "../table/table";
import { useState } from "react";

import { gql, useQuery, useLazyQuery } from "@apollo/client";

export default function Pending() {
  let logedUser = useSelector((store) => store.AuthSection.loggedUser);
  let [orders, setOrders] = useState([]);

  const User_Orders = gql`
    query ($email: String!) {
      getOrders(email: $email) {
        _id
        price
        title
        size
        img
        owner
        dic
      }
    }
  `;

  const { data, refetch } = useQuery(User_Orders, {
    variables: { email: logedUser.email },
    onCompleted: (data) => {
      if (data?.getOrders) {
        console.log(data.getOrders, "Orders [Default Array] from server ");
        setOrders(data.getOrders);
      } else {
        console.warn(data, "Orders not found ");
      }
    },
  });

  refetch();

  return (
    <div className="p-3 p-sm-5">
      <h1 className="text-center mb-5">Pending Orders</h1>

      {logedUser && orders.length > 0
        ? orders
            .filter((item) => item.owner === logedUser.email)
            .map((item) => {
              return (
                <Table item={item} key={item._id}>
                  <button className="btn btn-outline-warning border-3  d-sm-inline-block mx-auto">
                    Pending
                  </button>
                </Table>
              );
            })
        : null}
    </div>
  );
}
