import { Link } from "react-router-dom";
import logo from "../../Images/addidas.webp";
import { useDispatch, useSelector } from "react-redux";

export default function Navbar() {
  let dispatch = useDispatch();
  let Orders = useSelector((store) => store.ProductSection).orders;
  let UserLogedIn = useSelector((store) => store.AuthSection.loggedUser);
  return (
    // sticky-top
    <nav className="shadow-sm bg-white navbar navbar-expand-lg navbar-light ">
      <div className="container-fluid">
        <Link to={UserLogedIn ? "/home" : "/"} className="navbar-brand">
          <img src={logo} alt="logo" width={"70px"} />
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon" />
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
              {!UserLogedIn ? (
                <Link to="/login" className="nav-link fw-semibold active">
                  Log in
                </Link>
              ) : null}
            </li>
            <li className="nav-item">
              {!UserLogedIn ? (
                <Link to="/admin" className="nav-link fw-semibold">
                  Admin Panel
                </Link>
              ) : null}
            </li>
            <li className="nav-item">
              {UserLogedIn ? (
                <Link to="/home" className="nav-link fw-semibold active">
                  Home
                </Link>
              ) : null}
            </li>
            <li className="nav-item">
              {UserLogedIn ? (
                <Link to="/pending" className="nav-link fw-semibold">
                  Pending Orders
                </Link>
              ) : null}
            </li>
            <li className="nav-item dropdown">
              {UserLogedIn ? (
                <>
                  <div
                    className="nav-link fw-semibold dropdown-toggle"
                    id="navbarDropdown"
                    role="button"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                  >
                    Brands
                  </div>

                  <ul
                    className="dropdown-menu"
                    aria-labelledby="navbarDropdown"
                  >
                    <li>
                      <Link to="/brands/adidas" className="dropdown-item">
                        Adidas
                      </Link>
                    </li>
                    <li>
                      <Link to="/brands/bata" className="dropdown-item">
                        Bata
                      </Link>
                    </li>
                    <li>
                      <Link to="/brands/service" className="dropdown-item">
                        Service
                      </Link>
                    </li>
                    <li>
                      <Link to="/brands/nike" className="dropdown-item">
                        Nike
                      </Link>
                    </li>
                  </ul>
                </>
              ) : null}
            </li>
          </ul>
          {/* right side of navbar  */}
          <div className="navbar-nav">
            <li className="nav-item">
              {UserLogedIn ? (
                <Link
                  to="/"
                  className="nav-link fw-semibold active"
                  onClick={() => {
                    dispatch({ type: "USER_LOGGED_OUT" });
                  }}
                >
                  Log out
                </Link>
              ) : null}
            </li>
            <li className="nav-item px-3">
              {UserLogedIn ? (
                <Link
                  to="/cart"
                  className="nav-link fw-semibold position-relative"
                >
                  <svg
                    stroke="currentColor"
                    fill="currentColor"
                    strokeWidth="0"
                    viewBox="0 0 24 24"
                    className="fs-4"
                    height="1em"
                    width="1em"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path d="M20.0049 22H4.00488C3.4526 22 3.00488 21.5523 3.00488 21V3C3.00488 2.44772 3.4526 2 4.00488 2H20.0049C20.5572 2 21.0049 2.44772 21.0049 3V21C21.0049 21.5523 20.5572 22 20.0049 22ZM19.0049 20V4H5.00488V20H19.0049ZM9.00488 6V8C9.00488 9.65685 10.348 11 12.0049 11C13.6617 11 15.0049 9.65685 15.0049 8V6H17.0049V8C17.0049 10.7614 14.7663 13 12.0049 13C9.24346 13 7.00488 10.7614 7.00488 8V6H9.00488Z"></path>
                  </svg>
                  {Orders[0] ? (
                    <span
                      className="bg-danger text-white rounded-circle px-2 position-absolute "
                      style={{ right: "-8px", top: "-5px" }}
                    >
                      {Orders.length}
                    </span>
                  ) : null}
                </Link>
              ) : null}
            </li>
          </div>
          {/* right side of navbar  */}
        </div>
      </div>
    </nav>
  );
}
