// import React from 'react'

import { useDispatch, useSelector } from "react-redux";
import Table from "../table/table";
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";

import { gql, useQuery, useLazyQuery, useMutation } from "@apollo/client";

export default function Checkout() {
  let Orders = useSelector((store) => store.ProductSection).orders;
  let navigate = useNavigate();
  let dispatch = useDispatch();

  const User_Products = gql`
    mutation ($getProductsInput: [productsInput!]!) {
      saveOrders(input: $getProductsInput) {
        _id
        price
        title
        size
        owner
        dic
        img
      }
    }
  `;
  const [saveProducts, { data, error, loading }] = useMutation(User_Products, {
    variables: {},
    onCompleted: (data) => {
      if (data?.saveOrders) {
        navigate("/pending");
        dispatch({ type: "EMPTY_CART" });
      }
    },
  });

  return (
    <div className="container">
      {/* {Orders.length > 0 ? ( */}
      <h1 className="text-center text-primary my-4">Shopping Cart</h1>
      {/* ) : null} */}
      <div className="row m-0 w-100">
        <div className="col-md-12 p-0">
          {Orders.map((item) => {
            return (
              <Table item={item} key={item.id}>
                <svg
                  stroke="currentColor"
                  fill="currentColor"
                  strokeWidth="0"
                  viewBox="0 0 16 16"
                  className="mt-3 text-lg"
                  height="1em"
                  width="1em"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5Zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5Zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6Z"></path>
                  <path d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1ZM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118ZM2.5 3h11V2h-11v1Z"></path>
                </svg>
              </Table>
            );
          })}
        </div>
        {Orders.length > 0 ? (
          <div
            className="col-md-4 btn-dark btn rounded-pill fs-3 fw-semibold  mx-auto"
            onClick={() => {
              saveProducts({
                variables: { getProductsInput: Orders },
              });
            }}
          >
            Check Out Now
          </div>
        ) : (
          <>
            {/* <h1 className="h1 text-center">Cart is Empty</h1> */}
            <Link
              to="/brands/all"
              className="fs-3 fw-semibold col-4 mx-auto btn btn-dark"
            >
              Search Products
            </Link>
          </>
        )}
      </div>
    </div>
  );
}
