// import {} from "";

import { Link } from "react-router-dom";

export default function Footer() {
  return (
    <>
      {/* Footer */}
      <footer className="text-center text-lg-start bg-body-tertiary text-muted">
        {/* Section: Social media */}
        <section className="d-flex justify-content-center justify-content-lg-between p-4 border-bottom">
          {/* Right */}
          <div>
            <a href="#!" className="me-4 text-reset">
              <i className="fab fa-facebook-f" />
              Link
            </a>
            <a href="#!" className="me-4 text-reset">
              <i className="fab fa-twitter" />
              Link
            </a>
            <a href="#!" className="me-4 text-reset">
              <i className="fab fa-google" />
              Link
            </a>
            <a href="#!" className="me-4 text-reset">
              <i className="fab fa-instagram" />
              Link
            </a>
            <a href="#!" className="me-4 text-reset">
              <i className="fab fa-linkedin" />
              Link
            </a>
            {/* <a href="#!" className="me-4 text-reset">
              <i className="fab fa-github" />
              Link
            </a> */}
          </div>
          {/* Right */}
        </section>
        {/* Section: Social media */}
        {/* Section: Links  */}
        <section className="">
          <div className="container text-center text-md-start mt-5">
            {/* Grid row */}
            <div className="row mt-3">
              {/* Grid column */}
              <div className=" col-md-5 mb-4">
                {/* Content */}
                <h6 className="text-capitalize fs-4 fw-bold mb-4">
                  Feat wear <span className="text-primary">Paradies</span>
                </h6>
                <p>
                  Here you can use rows and columns to organize your footer
                  content. Lorem ipsum dolor sit amet, consectetur adipisicing
                  elit.
                </p>
              </div>
              {/* Grid column */}

              {/* Grid column */}
              <div className=" col-md-5 ms-md-5 mb-md-0 mb-4">
                {/* Links */}
                <h6 className="text-capitalize fw-bold mb-4 ms-3">Contact</h6>
                <p>
                  <i className="fas fa-home me-3" /> New York, NY 10012, US
                </p>
                <p>
                  <i className="fas fa-envelope me-3" />
                  someone@example.com
                </p>
                <p>
                  <i className="fas fa-phone me-3" /> + 92 327 569 2876
                </p>
                <p>
                  <i className="fas fa-print me-3" /> + 01 978 567 8270
                </p>
              </div>
              {/* Grid column */}
            </div>
            {/* Grid row */}
          </div>
        </section>
        {/* Section: Links  */}
        {/* Copyright */}
        <div
          className="text-center p-4"
          style={{ backgroundColor: "rgba(0, 0, 0, 0.15)" }}
        >
          © 2024 Copyright:
          <Link to="/" className="text-reset ms-2 fw-bold">
            Abdul-Rehman-INC
          </Link>
        </div>
        {/* Copyright */}
      </footer>
      {/* Footer */}
    </>
  );
}
