// import axios from "axios";
import { useForm } from "react-hook-form";
import { Link, useNavigate } from "react-router-dom";
import { toast } from "react-toastify";

import { gql, useMutation } from "@apollo/client";

export default function Signup() {
  let { register, handleSubmit } = useForm();
  let navigate = useNavigate();

  const Save_User = gql`
    mutation ($createUserInput: createInput!) {
      createUser(input: $createUserInput) {
        email
        first
        last
        password
      }
    }
  `;
  const [saveUser, { data: signUpData }] = useMutation(Save_User, {
    onCompleted: (data) => {
      if (data) {
        console.log(data, "ya GraphQl Ka Data ha ...........");
        navigate("/login");
        toast.success(
          `Perfect now login ${data?.createUser?.first?.toUpperCase()}`
        );
      } else if (!data.createUser) {
        toast.error('Oop"s Something went Wrong try again');
      }
    },
  });

  function formData(formData) {
    saveUser({
      variables: { createUserInput: formData },
    });
  }

  return (
    <div>
      <form
        onSubmit={handleSubmit(formData)}
        className="p-3 p-sm-5 row w-100 m-0"
      >
        <div className=" mb-sm-4 fw-semibold display-3 ">
          Create Your Account
        </div>
        <p className="fw-semibold  text-muted mb-4">
          If You Signed Up Earlier Then Just {"->"}
          <Link to="/login" className="">
            {" "}
            Log In
          </Link>
        </p>
        <div className="col-md-6 my-2 fw-semibold ">
          First Name
          <input
            {...register("first", { required: true })}
            type="text"
            placeholder="Abdul"
            className="w-100 col-sm-3 p-3  fs-5 border rounded-3 bg-light "
          />
        </div>
        <div className="col-md-6 my-2 fw-semibold ">
          Last Name
          <input
            {...register("last", { required: true })}
            type="text"
            placeholder="Rehman"
            className="w-100 col-sm-3 p-3  fs-5 border rounded-3 bg-light "
          />
        </div>
        <div className="col-md-12 my-2 fw-semibold ">
          Email
          <input
            {...register("email", { required: true })}
            type="email"
            required
            placeholder="someone@email.com"
            className="w-100 col-sm-3 p-3  fs-5 border rounded-3 bg-light "
          />
        </div>
        <div className="col-md-12 my-2 fw-semibold ">
          Password
          <input
            {...register("password", { required: true })}
            type="text"
            required
            placeholder="Minimum 6 character's"
            className="w-100 col-sm-3 p-3  fs-5 border rounded-3 bg-light "
          />
        </div>
        <button className="col-sm-4 mx-auto p-3 mt-4 fs-5 fw-bold border-0 rounded-3 bg-primary text-white">
          Sign Up
        </button>
      </form>
    </div>
  );
}
