function ProductSection(olData = initialProducts, newData) {

    try {
        olData = { ...olData, products: [...olData.products] };

        if (newData.type === 'SEARCH_PRODUCT') {
            olData.searched = newData.payload;
        } else if (newData.type === 'ADD_TO_CART') {
            olData.orders.unshift(newData.payload);
            toast.info("Product Added to Cart");
        } else if (newData.type === 'DEL_FROM_CART') {
            let slice = olData.orders.findIndex((a1) => a1.id === newData.payload.id)
            olData.orders.splice(slice, 1);
            toast.warning("Product Removed");
        } else if (newData.type === 'EMPTY_CART') {
            olData.orders = [];
            toast.warning("Order's in Queue");
        }

    } catch (e) {
        console.log(e.message)
    }

    return olData;
}



let initialAuthData = { loggedUser: null }

function AuthSection(olData = initialAuthData, newData) {

    olData = { ...olData };

    if (newData.type === 'USER_LOGIN_HOGYA') {
        olData.loggedUser = newData.payload;
        // console.log(olData.loggedUser);

    } else if (newData.type === 'USER_LOGGED_OUT') {
        localStorage.removeItem('userId');
        olData.loggedUser = null;
        toast.warning("Loged out");
    }
    return olData;
}

let allSections = combineReducers({ ProductSection, AuthSection });

export let store = createStore(allSections);