import React from "react";
import { useDispatch } from "react-redux";

export default function Table({ item, children }) {
  let dispatch = useDispatch();
  return (
    <>
      {/* p-sm-5 p-3 */}
      <div className="">
        {/* border remove  .border*/}
        <div className="row w-100 m-0 pb-3 mb-3 border-3  border-bottom">
          <img className="col-2 col-lg-1 p-0 px-sm-2" src={item.img} alt="" />
          <div className="col-7 col-sm-6 col-md-8">
            <div className="h3 ">{item.title}</div>
            <p className="d-md-block d-none">{item.dic}</p>
            <span className="me-3 me-sm-5">Size: {item.size}</span>
            <span className="d-none d-sm-inline-block">Quantity: 1</span>
          </div>
          <div className="col-2 ms-0 ms-sm-auto h4 p-0 px-sm-2">
            <div className=" text-center"> ${item.price}</div>
            <div
              className="text-center mt-3"
              style={{ cursor: "pointer" }}
              onClick={() => {
                if (children.type === "svg") {
                  dispatch({ type: "DEL_FROM_CART", payload: item });
                }
              }}
            >
              {children}
            </div>
          </div>
        </div>
        {/*  */}
      </div>
    </>
  );
}
