import { Link, useParams } from "react-router-dom";
import Card from "../card/card";
import { useDispatch, useSelector } from "react-redux";

export default function Brands() {
  let params = useParams();
  let search = useSelector((store) => store.ProductSection).searched;
  let sendData = useDispatch();
  let userProducts = useSelector((store) => store.ProductSection.products);
  return (
    <>
      {/* search box  */}
      <div className="container">
        <input
          onChange={(evt) => {
            sendData({
              type: "SEARCH_PRODUCT",
              payload: evt.target.value.toLowerCase(),
            });
          }}
          className="fs-5 py-2 px-4 my-4 w-100 fw-semibold border rounded-pill text-success"
          type="search"
          placeholder="Search Products.."
        />
      </div>
      <div className=" row w-100">
        {/* brands linking  */}
        <div className=" ms-md-5 ms-0 col-12 col-md-2 d-md-block d-none   rounded-3">
          <Link to="/brands/all" className="text-dark disabled">
            <div className="my-1 py-2 px-2 rounded fs-4 fw-bold border border-2">
              All Brands
            </div>
          </Link>
          <Link to="/brands/nike" className="text-muted">
            <div className="my-1 py-1 px-2 rounded fs-5 fw-semibold border border-2">
              Nike
            </div>
          </Link>
          <Link to="/brands/service" className="text-muted">
            <div className="my-1 py-1 px-2 rounded fs-5 fw-semibold border border-2">
              Service
            </div>
          </Link>
          <Link to="/brands/bata" className="text-muted">
            <div className="my-1 py-1 px-2 rounded fs-5 fw-semibold border border-2">
              Bata
            </div>
          </Link>
          <Link to="/brands/adidas" className="text-muted">
            <div className="my-1 py-1 px-2 rounded fs-5 fw-semibold border border-2">
              Adidas
            </div>
          </Link>
        </div>
        <div className=" ms-md-2 ms-0 col-12 col-md-9">
          {/* heading  */}
          <h1 className="my-4 d-flex  heading">
            <span className="h2 me-auto me-sm-0 px-3 px-md-0 text-capitalize">
              {params.brand ? params.brand : "All Brands"}
            </span>
            <span className="h2 d-sm-block  me-auto text-light px-3 px-md-4 px-lg-5">
              Check our new products
            </span>
          </h1>
          {/* products */}
          <div className="row w-100 m-0">
            {userProducts
              .filter((item) =>
                params.brand === "all"
                  ? true
                  : params.brand
                  ? item.brand === params.brand
                  : null
              )
              .filter((item) => item.title.toLowerCase().includes(search))
              .map((product) => {
                return <Card key={product.id} item={product} />;
              })}
          </div>
        </div>
      </div>
    </>
  );
}
